<template>
   <div id="canvas">
      <div id="canvas-menu">
         <el-button-group>
            <el-button
               type="warning"
               icon="el-icon-edit-outline"
               plain
               @click="onPush({name: 'root'})"
            />
            <el-button
               type="info"
               icon="el-icon-d-arrow-left"
               :disabled="!canUndo"
               @click="undo"
               plain
            >
               Назад
            </el-button>
            <el-button
               type="info"
               :disabled="!canRedo"
               @click="redo"
               plain
            >
               Вперёд  <i class="el-icon-d-arrow-right"/>
            </el-button>

            <a class="downloadLink el-button is-plain el-button--success" @click.capture="getFile($event.target)">
               getJSON<i class="el-icon-download"/>
            </a>
            <input type="file" class="inputfile" id="inputfile" @change="setFile($event)">
            <label for="inputfile" class="downloadLink el-button is-plain el-button--success">
               setJSON<i class="el-icon-upload2"/>
            </label>
            <el-button
               type="warning"
               plain
               @click="save()"
            >
               Cохранить
               <i class="el-icon-download"/>
            </el-button>
            <el-button
               type="warning"
               plain
               @click="modal()"
            >
               Cохранить как
               <i class="el-icon-download"/>
            </el-button>
         </el-button-group>
         <el-button
            type="danger"
            icon="el-icon-remove"
            @click="clear(), saveSnapshot()"
         >
            Очистить
         </el-button>
      </div>
      <form id="canvas-form" :name="name">
         <app-container
            :row="row"
            :styles="styles"
            :value="{
               name: name,
               type: 'AppContainer',
               childs: row,
               options: {
                  styles: styles
               }
            }"
            @push="onPush($event), saveSnapshot()"
            @change="setValue($event), saveSnapshot()"
         ></app-container>
      </form>
      <app-modal-box
         v-if="modalBox.show"
         @close="modalBox.show = false"
      >
         <form>
            <components
               v-for="(value, key) of modalBox.obj"
               :key="key"
               :is="key"
               :value="value"
               @change="setValue({
                  pos: modalBox.pos,
                  val: $event,
                  key: key,
                  type: modalBox.type
               }), saveSnapshot()"
               @addOpt="addOpt({
                  pos: modalBox.pos,
                  key: key,
                  type: modalBox.type
               }), saveSnapshot()"
               @changeOpt="changeOpt({
                  pos: modalBox.pos,
                  key: key,
                  type: modalBox.type,
                  val: $event.val,
                  index: $event.index
               }), saveSnapshot()"
               @delOpt="delOpt({
                  pos: modalBox.pos,
                  key: key,
                  type: modalBox.type,
                  index: $event
               }), saveSnapshot()"
            ></components>
         </form>
      </app-modal-box>
   </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import AppContainer from './Container.vue'
import AppModalBox from './ModalBox.vue'
// styles
import Background from './styles/Background.vue'
import Flex from './styles/Flex.vue'
import Size from './styles/Size.vue'
import Margin from './styles/Margin.vue'
import Padding from './styles/Padding.vue'
import Border from './styles/Border.vue'
import Font from './styles/Font.vue'
import Overflow from './styles/Overflow.vue'
// value
import Value from './values/Value.vue'
import MyLabel from './values/Label.vue'
import MyOption from './values/Option.vue'

export default {
  components: {
    AppContainer,
    AppModalBox,
    Background,
    Flex,
    Size,
    Margin,
    Padding,
    Border,
    Font,
    Overflow,
    Value,
    MyLabel,
    MyOption
  },
  data: () => ({
    modalBox: {
      show: false,
      obj: {},
      pos: [],
      type: ''
    }
  }),
  computed: {
    ...mapGetters({
      name: 'getRootName',
      styles: 'getRootStyle',
      row: 'getRootValue',
      get: 'getItem'

    })
  },
  methods: {
    ...mapMutations({
      addItem: 'addItem',
      delItem: 'delItem',
      setValue: 'setItemProps',
      addOpt: 'addOpt',
      changeOpt: 'changeOpt',
      delOpt: 'delOpt',
      saveItem: 'saveItem',
      getFile: 'getJSON',
      setFile: 'setJSON',
      clear: 'clearAll',
      save: 'save'
    }),
    onPush (e) {
      let name = e.name
      let ev = {
        pos: e.pos,
        val: e.val
      }
      if (name === 'add') { this.addItem(ev) } else if (name === 'editStyle') {
        this.modalBox.pos = e.pos
        this.modalBox.obj = this.get(e.pos).options.styles
        this.modalBox.show = true
        this.modalBox.type = 'styles'
      } else if (name === 'editValue') {
        this.modalBox.pos = e.pos
        this.modalBox.obj = this.get(e.pos).options.values
        this.modalBox.show = true
        this.modalBox.type = 'values'
      } else if (name === 'root') {
        this.modalBox.pos = ''
        this.modalBox.obj = this.styles
        this.modalBox.show = true
        this.modalBox.type = 'styles'
      } else if (name === 'del') { this.delItem(ev) } else if (name === 'save') { this.saveItem(ev) } else { console.log('неизвестный ивент') }
    },
    modal () {
      let res = prompt('Введите имя сохранения', Date.now())
      this.save(res)
    }
  }
}
</script>

<style lang="scss" scoped>

   #canvas {
      position: relative;
      flex: 1 1 auto;
      background-color: #b3b3b3;
      width: 100%;

      &-form {
         margin-top: 48px;
         height: calc(100% - 48px);
         overflow-y: scroll;
      }

      &-menu {
         position: fixed;
         display: flex;
         width: 100%;
         background-color: #BBBDF1;
         height: 48px;
         justify-content: space-between;
         align-items: center;
         padding: 0 40px;
         box-sizing: border-box;
         z-index: 10000;
         box-shadow: 0 0 5px rgba(0,0,0,0.8);
      }
   }

   .downloadLink {
      box-sizing: border-box;
      height: 40px;
      display: block;
      text-decoration: none;
      z-index: 100;
   }

   .inputfile {
      display: none;
   }
</style>
