<template>
   <el-form label-width="180px">
      <el-form-item label="Значение по умочанию">
         <el-input v-model="val"/>
      </el-form-item>
   </el-form>
</template>

<script>
export default {
  props: ['value'],
  computed: {
    val: {
      get () {
        return this.value
      },
      set (v) {
        this.$emit('change', v)
      }
    }
  }
}
</script>
