<template>
   <div id="side-panel" class="accordion">
      <div class="accordion-item">
         <div class="accordion-header" @click.prevent="activePanel = 'components'">
            <span>Панель компонентов</span><i class="el-icon-arrow-right"/>
         </div>
         <transition>
            <div class="accordion-content" v-show="activePanel === 'components'">
               <draggable
                  :list="components"
                  :options="{
                     sort: false,
                     group: {
                        name: 'list',
                        pull: 'clone',
                        put: false
                     }
                  }"
                  :clone="(original)=> JSON.parse(JSON.stringify(original))"
               >
                  <el-card
                     class="box-card toolbar-item"
                     v-for="(item, index) of components"
                     :key="index"
                     shadow="hover"
                  >
                     {{item.name}}
                  </el-card>
               </draggable>
            </div>
         </transition>
      </div>
      <div class="accordion-item">
         <div class="accordion-header" @click.prevent="activePanel = 'userComponents'">
            <span>Пользовательские компоненты</span><i class="el-icon-arrow-right"/>
         </div>
         <transition>
         <div class="accordion-content" v-show="activePanel === 'userComponents' && userComponents.length > 0">
            <draggable
               :list="userComponents"
               :options="{
                  group: {
                     name: 'list',
                     pull: 'clone',
                     put: false
                  }
               }"
               :clone="(original)=> JSON.parse(JSON.stringify(original))"
            >
               <el-card
                  class="box-card toolbar-item"
                  v-for="(item, index) of userComponents"
                  :key="index"
                  shadow="hover"
               >
                  <div class="toolbar-flex">
                     <span
                        :contenteditable="!getLS.filter(el => el.id == item.id)[0].disabled"
                        @blur="setName({
                           name: $event.target.innerText,
                           index: index
                        })"
                     >{{item.name}}</span>
                     <div class="button-group">
                        <el-button
                           :type="(getLS.filter(el => el.id == item.id)[0].disabled) ? 'danger' : 'success'"
                           :icon="(getLS.filter(el => el.id == item.id)[0].disabled) ? 'el-icon-minus' : 'el-icon-plus'"
                           size="mini"
                           circle
                           @click.prevent="toggleLS(item.id)"
                        />
                        <el-button
                           type="danger"
                           icon="el-icon-delete"
                           size="mini"
                           circle
                           :disabled="getLS.filter(el => el.id == item.id)[0].disabled"
                           @click.prevent="removeComp(index), saveSnapshot()"
                        />
                     </div>
                  </div>
               </el-card>
            </draggable>
         </div>
         </transition>
      </div>
      <div class="accordion-item">
         <div class="accordion-header" @click.prevent="activePanel = 'some'">
            <span>Сохранения</span><i class="el-icon-arrow-right"/>
         </div>
         <transition>
            <div class="accordion-content" v-show="activePanel === 'some' && Object.keys(getSave).length > 0">
               <div v-for="(item, index) of getSave.user" :key="item.name + index">
                  {{item.name}}
               </div>
               <div v-for="(item, index) of getSave.date" :key="item.name + index">
                  {{item.name}}
               </div>
            </div>
         </transition>
      </div>
   </div>
</template>

<script>
import draggable from 'vuedraggable'
import { mapGetters, mapMutations } from 'vuex'

export default {
  components: {
    draggable
  },
  beforeMount () {
    this.getCounter()
    this.loadUserComponents()
  },
  data: () => ({
    activePanel: ''
  }),
  computed: {
    ...mapGetters({
      components: 'getComponents',
      userComponents: 'getUserComponents',
      test: 'getRootValue',
      getLS: 'getLS'
    }),
    getSave () {
      let save = JSON.parse(localStorage.getItem('save'))
      if (save === null || undefined || NaN) { save = [] }
      console.log(save)
      return save
    }
  },
  methods: {
    ...mapMutations({
      removeComp: 'delUserComponents',
      setName: 'setName',
      setToLS: 'saveToLS',
      getCounter: 'loadCounter',
      loadUserComponents: 'loadUserComponents',
      deleteFromLS: 'deleteFromLS'
    }),
    toggleLS (id) {
      let check = this.getLS.filter(el => el.id == id)[0].disabled
      if (!check) { this.setToLS(id) } else if (check) { this.deleteFromLS(id) }
    },
    takeID (id) {
      let boolean = false
      if (this.getLS !== null) {
        let arr = this.getLS.filter(el => el.id === id)
        if (arr.length > 0) { boolean = true }
      }
      return boolean
    }
  }
}
</script>

<style lang="scss" scoped>

   #side-panel {
      margin-top: 48px;
      height: calc(100% - 48px);
      min-width: 400px;
      max-width: 400px;
      width: 400px;
      display: flex;
      flex-flow: column nowrap;
      max-height: 100vh;
      box-sizing: border-box;
      overflow-y: hidden;
   }

   .toolbar {
      background-color: #b3b3b3;
      &-item {
         margin: 5px;
      }
      &-flex {
         display: flex;
         justify-content: space-between;
         align-items: center;
      }
   }

   .accordion {
      background-color: #BBBDF1;
      overflow-y: hidden;
      overflow-x: hidden;

      &-item {
         min-height: 48px;
         flex: 0 1 auto;
         overflow-x: hidden;
         overflow-y: auto;
         display: flex;
         flex-flow: column nowrap;
      }

      &-header {
         height: 48px;
         width: 100%;
         background-color: #BBBDF1;
         color: #fff;
         padding: 0 25px;
         box-sizing: border-box;
         display: flex;
         justify-content: space-between;
         align-items: center;
         cursor: pointer;
         font-size: 18px;
         border-bottom: 1px solid #fff;
      }

      &-content {
         background-color: #fff;
         padding: 20px;
         flex: 1 1 auto;
      }
   }

   .accordion {
      &-enter-active {
         max-height: 0;
         transition: max-height 2s;
         overflow: hidden;
      }

      &-enter-to {
         max-height: 100vh;
      }
      &-leave-active {
         max-height: 100vh;
         transition: max-height 3s;
         overflow: hidden;
      }
      &-leave-to {
         max-height: 0;
      }
   }

</style>
