<template>
   <el-form label-width="180px">
      <el-form-item label="Переполнение">
         <el-checkbox v-model="checked"/>
      </el-form-item>
      <el-form-item label="Общее" v-if="checked">
         <el-select v-model="overflow" placeholder="наследуется">
            <el-option label="наследуется" value="inherit"/>
            <el-option label="автоматически" value="auto"/>
            <el-option label="видно" value="visible"/>
            <el-option label="скрыть" value="hidden"/>
            <el-option label="скрол" value="scroll"/>
         </el-select>
      </el-form-item>
   </el-form>
</template>

<script>
export default {
  props: ['value'],
  data: () => ({
    checked: false
  }),
  computed: {
    overflow: {
      get () {
        let ovr = this.value
        if (ovr === 'inherit') { ovr = undefined }
        return ovr
      },
      set (v) {
        if (v === undefined) { this.$emit('change', 'inherit') } else { this.$emit('change', v) }
      }
    }
  }
}
</script>
