<template>
   <el-form label-width="180px">
      <el-form-item label="Внешний отступ">
         <el-checkbox v-model="checked"/>
      </el-form-item>
      <el-form-item label="Сверху" v-if="checked">
         <el-input-number v-model="top" :step="stepTop"/>
         <el-select class="size" v-model="type.top">
            <el-option label="px" value="px"/>
            <el-option label="cm" value="cm"/>
            <el-option label="%" value="%"/>
         </el-select>
      </el-form-item>
      <el-form-item label="Слева" v-if="checked">
         <el-input-number v-model="left" :step="stepLeft"/>
         <el-select class="size" v-model="type.left">
            <el-option label="px" value="px"/>
            <el-option label="cm" value="cm"/>
            <el-option label="%" value="%"/>
         </el-select>
      </el-form-item>
      <el-form-item label="Справа" v-if="checked">
         <el-input-number v-model="right" :step="stepRight"/>
         <el-select class="size" v-model="type.right">
            <el-option label="px" value="px"/>
            <el-option label="cm" value="cm"/>
            <el-option label="%" value="%"/>
         </el-select>
      </el-form-item>
      <el-form-item label="Снизу" v-if="checked">
         <el-input-number v-model="bottom" :step="stepBottom"/>
         <el-select class="size" v-model="type.bottom">
            <el-option label="px" value="px"/>
            <el-option label="cm" value="cm"/>
            <el-option label="%" value="%"/>
         </el-select>
      </el-form-item>
   </el-form>
</template>

<script>
export default {
  props: ['value'],
  data: () => ({
    checked: false,
    type: {
      top: 'px',
      left: 'px',
      right: 'px',
      bottom: 'px'
    }
  }),
  computed: {
    stepTop () {
      let step = 1
      if (this.type.top === 'cm') { step = 0.1 } else if (this.type.top === '%') { step = 0.1 }
      return step
    },
    stepLeft () {
      let step = 1
      if (this.type.left === 'cm') { step = 0.1 } else if (this.type.left === '%') { step = 0.1 }
      return step
    },
    stepRight () {
      let step = 1
      if (this.type.right === 'cm') { step = 0.1 } else if (this.type.right === '%') { step = 0.1 }
      return step
    },
    stepBottom () {
      let step = 1
      if (this.type.bottom === 'cm') { step = 0.1 } else if (this.type.bottom === '%') { step = 0.1 }
      return step
    },
    top: {
      get () {
        return parseFloat(this.value.top.replace(/[^-.0-9]/gim, ''))
      },
      set (v) {
        this.$emit('change', { top: v + this.type.top })
      }
    },
    left: {
      get () {
        return parseFloat(this.value.left.replace(/[^-.0-9]/gim, ''))
      },
      set (v) {
        this.$emit('change', { left: v + this.type.left })
      }
    },
    right: {
      get () {
        return parseFloat(this.value.right.replace(/[^-.0-9]/gim, ''))
      },
      set (v) {
        this.$emit('change', { right: v + this.type.right })
      }
    },
    bottom: {
      get () {
        return parseFloat(this.value.bottom.replace(/[^-.0-9]/gim, ''))
      },
      set (v) {
        this.$emit('change', { bottom: v + this.type.bottom })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
   .size {
      width: 80px;
   }
</style>
