<template>
   <draggable
      class="container"
      :style="styles"
      :list="row"
      :options="{
         group: {
            name: 'list',
            pull: true,
            put: true
         },
         disabled: false
      }"
   >
      <div
         class="component-wrapper"
         :class="(ttShow === index) ? 'component-wrapper-active' : ''"
         v-for="(component, index) of row"
         :key="index"
         :options="{
            disabled: true
         }"
         :style="{
            'max-height' : component.options.styles.size['max-height'] || '',
            'max-width' : component.options.styles.size['max-width'] || '',
            'min-height' : component.options.styles.size['min-height'] || '',
            'min-width' : component.options.styles.size['min-width'] || ''
         }"
         @mouseover.stop="ttShow = index"
         @mouseout.stop="ttShow = ''"
      >
         <app-tooltip
            v-if="ttShow === index"
            @push="push($event, index)"
         ></app-tooltip>
         <components
            :is="component.type"
            :value="component"
            @push="push($event, index)"
            @change="change($event, index)"
         ></components>
      </div>
      <draggable
         class="container-empty"
         :options="{
            sort: false,
            group: {
               name: 'list',
               pull: false,
               put: true
            },
            disabled: false
         }"
         v-model="fakeList"
      >
         +
      </draggable>
   </draggable>
</template>

<script>
import draggable from 'vuedraggable'
import AppTooltip from './Tooltip.vue'
import AppHeader from './libs/Header.vue'
import AppTextInput from './libs/TextInput.vue'
import AppSelect from './libs/Select.vue'

export default {
  name: 'AppContainer',
  components: {
    draggable,
    AppTooltip,
    AppHeader,
    AppTextInput,
    AppSelect
  },
  props: {
    value: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    ttShow: ''
  }),
  computed: {
    row () {
      return this.value.childs
    },
    styles () {
      let style = this.value.options.styles
      return {
        // bcg
        'background': style.background || 'inherit',
        // flex
        'display': 'flex',
        'flex-direction': style.flex.direction || 'column',
        'flex-wrap': style.flex.wrap || 'wrap',
        'justify-content': style.flex.justifyContent || 'flex-start',
        'align-items': style.flex.alignItems || 'stretch',
        'flex-shrink': style.flex.shrink || '1',
        'flex-grow': style.flex.grow || '1',
        'flex-basis': style.flex.basis || 'auto',
        'align-self': style.flex.alignSelf || '',
        // size
        'min-height': style.size['min-height'] || '',
        'max-height': style.size['max-height'] || '',
        'min-width': style.size['min-width'] || '',
        'max-width': style.size['max-width'] || '',
        // margin
        'margin-top': style.margin.top || '',
        'margin-left': style.margin.left || '',
        'margin-right': style.margin.right || '',
        'margin-bottom': style.margin.bottom || '',
        // padding
        'padding-top': style.padding.top || '',
        'padding-left': style.padding.left || '',
        'padding-right': style.padding.right || '',
        'padding-bottom': style.padding.bottom || '',
        // border
        'border-top-width': style.border['top-width'] || '',
        'border-top-style': style.border['top-style'] || '',
        'border-top-color': style.border['top-color'] || '',
        'border-left-width': style.border['left-width'] || '',
        'border-left-style': style.border['left-style'] || '',
        'border-left-color': style.border['left-color'] || '',
        'border-right-width': style.border['right-width'] || '',
        'border-right-style': style.border['right-style'] || '',
        'border-right-color': style.border['right-color'] || '',
        'border-bottom-width': style.border['bottom-width'] || '',
        'border-bottom-style': style.border['bottom-style'] || '',
        'border-bottom-color': style.border['bottom-color'] || '',
        'border-radius': style.border.radius || '',
        'border-top-left-radius': style.border['top-left-radius'] || '',
        'border-top-right-radius': style.border['top-right-radius'] || '',
        'border-bottom-left-radius': style.border['bottom-left-radius'] || '',
        'border-bottom-right-radius': style.border['bottom-right-radius'] || '',
        // font
        'font-family': style.font.family || 'inherit',
        'font-style': style.font.style || 'inherit',
        'font-weight': style.font.weight || 'inherit',
        'font-size': style.font.size || 'inherit',
        'color': style.font.color || 'inherit',
        'text-transform': style.font.register || 'inherit',
        // overflow
        'overflow': style.overflow || 'auto'

      }
    },
    fakeList: {
      get () {
        return []
      },
      set (v) {
        this.$emit('push', {
          name: 'add',
          val: v[0],
          pos: []
        })
      }
    }
  },
  methods: {
    push (e, i) {
      let pos = e.pos
      let name = e.name
      let val = e.val
      pos.unshift(i)
      this.$emit('push', {
        name: name,
        val: val,
        pos: pos
      })
    },
    change (e, i) {
      let pos = e.pos
      pos.unshift(i)
      let newEvent = {
        pos: pos,
        val: e.val,
        key: e.key,
        type: e.type
      }
      this.$emit('change', newEvent)
    }
  }
}
</script>

<style lang="scss" scoped>
   .container {
      box-sizing: border-box;

      &-empty {
         display: flex;
         justify-content: center;
         align-items: center;
         font-size: 40px;
         background-color: whitesmoke;
         color: grey;
         min-height: 120px;
         min-width: 80px;
         flex: 1 1 auto;
         box-sizing: border-box;
         font-weight: normal;
      }
   }

   .component-wrapper {
      position: relative;
      display: flex;
      flex-flow: column nowrap;
      flex: 100 1 auto;
      box-sizing: border-box;

      &-active {
         outline: 3px solid #BBBDF1;
      }
   }
</style>
