<template>
   <el-form label-width="180px">
      <el-form-item label="Ширина">
         <el-input-number v-model="width" :step="0.1" :min="0"/>
         <el-select class="size" v-model="type">
            <el-option label="px" value="px"/>
            <el-option label="cm" value="cm"/>
            <el-option label="%" value="%"/>
         </el-select>
      </el-form-item>
      <el-form-item label="Тип линии">
         <el-select v-model="lineType">
            <el-option label="cплошная" value="solid"/>
            <el-option label="пунктир" value="dashed"/>
            <el-option label="точки" value="dotted"/>
            <el-option label="двойная" value="double"/>
         </el-select>
      </el-form-item>
      <el-form-item label="Цвет">
         <el-color-picker v-model="color"/>
      </el-form-item>
   </el-form>
</template>
<script>
export default {
  props: ['value', 'dir'],
  data: () => ({
    objWidth: {},
    objStyle: {},
    objColor: {},
    type: 'px'
  }),
  beforeMount () {
    this.objWidth[this.dir + 'width'] = ''
    this.objStyle[this.dir + 'style'] = ''
    this.objColor[this.dir + 'color'] = ''
  },
  computed: {
    width: {
      get () {
        let result
        if (this.value[0] !== '') { [result] = this.value } else { result = '0' }
        return parseFloat(result.replace(/[^.0-9]/gim, ''))
      },
      set (v) {
        this.set('objWidth', 'width', v + this.type)
        this.$emit('change', this.objWidth)
      }
    },
    lineType: {
      get () {
        return this.value[1]
      },
      set (v) {
        this.set('objStyle', 'style', v),
        this.$emit('change', this.objStyle)
      }
    },
    color: {
      get () {
        let color = null
        if (this.value[2]) { color = this.value[2] }
        return color
      },
      set (v) {
        this.set('objColor', 'color', v),
        this.$emit('change', this.objColor)
      }
    }
  },
  methods: {
    set (o, k, v) {
      if (typeof (this.dir) === 'string') { this[o][this.dir + k] = v } else if (this.dir instanceof Array) {
        for (let i = 0; i < this.dir.length; i++) { this[o][this.dir[i] + k] = v }
      }
    }
  }

}
</script>

<style lang="scss" scoped>
   .size {
      width: 80px;
   }
</style>
