<template>
   <el-form label-width="180px">
      <el-form-item label="Граница">
         <el-checkbox v-model="checked.default"/>
      </el-form-item>
      <border-template v-if="checked.default"
         :dir="['top-', 'left-', 'right-', 'bottom-']"
         :value="[value['top-width'], value['top-style'], value['top-color']]"
         @change="$emit('change', $event)"
      >
      </border-template>
      <template v-for="(name, key) of names">
         <el-form-item :label="name" :key="key" v-if="checked.default">
            <el-checkbox v-model="checked[key]"/>
         </el-form-item>
         <border-template
            :key="name"
            v-if="checked[key] && checked.default"
            :dir="key + '-'"
            :value="[
               value[key + '-width'],
               value[key + '-style'],
               value[key + '-color']
            ]"
            @change="$emit('change', $event)"
         >
         </border-template>
      </template>
   </el-form>
</template>

<script>
import BorderTemplate from './BorderTemplate.vue'
export default {
  components: {
    BorderTemplate
  },
  props: ['value'],
  data: () => ({
    names: {
      top: 'Верхняя граница: ',
      left: 'Левая граница: ',
      right: 'Правая граница: ',
      bottom: 'Нижния граница: '
    },
    checked: {
      default: false,
      top: false,
      left: false,
      right: false,
      bottom: false
    }
  })
}
</script>
