import Vue from 'vue'
import App from './App.vue'
import store from './store'
import './plugins/element.js'
import VuexHistory from 'vuex-history'

Vue.use(VuexHistory)

const maxHistoryLength = 50
const watchStateNames = ['json', 'toolbox']
const vuexHistory = new VuexHistory(store, watchStateNames, maxHistoryLength)

Vue.mixin({
  computed: {
    canUndo () {
      return vuexHistory.canUndo
    },
    canRedo () {
      return vuexHistory.canRedo
    },
    getHistory () {
      return vuexHistory.getHistory
    }
  },
  methods: {
    clearHistory () {
      vuexHistory.clearHistory()
    },
    saveSnapshot () {
      vuexHistory.saveSnapshot()
    },
    undo () {
      vuexHistory.undo()
    },
    redo () {
      vuexHistory.redo()
    }
  }
})

Vue.config.productionTip = false

new Vue({
  store,
  render: h => h(App)
}).$mount('#constructor')
