<template>
   <el-form label-width="180px">
      <el-form-item label="Шрифт">
         <el-checkbox v-model="checked"/>
      </el-form-item>
      <el-form-item label="Используемый шрифт" v-if="checked">
         <el-select v-model.lazy="fontType" placeholder="Наследуется">
            <el-option label="наследуется" value="inherit"/>
            <el-option label="Arial" value="Arial"/>
            <el-option lebel="New Roman" value="New Roman"/>
         </el-select>
      </el-form-item>
      <el-form-item label="Курсив" v-if="checked">
         <el-select v-model.lazy="fontStyle" placeholder="Наследуется">
            <el-option label="наследуется" value="inherit"/>
            <el-option label="да" value="italic"/>
            <el-option label="нет" value="normal"/>
         </el-select>
      </el-form-item>
      <el-form-item label="Жирность" v-if="checked">
         <el-select v-model.lazy="fontWeight" placeholder="Наследуется">
            <el-option label="наследуется" value="inherit"/>
            <el-option label="да" value="bold"/>
            <el-option label="нет" value="normal"/>
         </el-select>
      </el-form-item>
      <el-form-item label="Размер" v-if="checked">
         <el-input-number v-model="fontSize" :min="0" placeholder="Наследуется"/>
      </el-form-item>
      <el-form-item label="Цвет" v-if="checked">
         <el-color-picker v-model.lazy="fontColor"/>
      </el-form-item>
      <el-form-item label="Регистр" v-if="checked">
         <el-select v-model.lazy="fontReg" placeholder="Наследуется">
            <el-option label="наследуется" value="inherit"/>
            <el-option label="с заглавной" value="capitalize"/>
            <el-option label="в верхнем регистре" value="uppercase"/>
            <el-option label="в нижнем регистре" value="lowercase"/>
            <el-option label="не задан" value="none"/>
         </el-select>
      </el-form-item>
   </el-form>
</template>

<script>
export default {
  props: ['value'],
  data: () => ({
    checked: false,
    font: {}
  }),
  computed: {
    fontType: {
      get () {
        let family = this.value.family
        if (family === 'inherit') { family = undefined }
        return family
      },
      set (v) {
        if (v === undefined) { this.$emit('change', { family: 'inherit' }) } else { this.$emit('change', { family: v }) }
      }
    },
    fontStyle: {
      get () {
        let style = this.value.style
        if (style === 'inherit') { style = undefined }
        return style
      },
      set (v) {
        if (v === undefined) { this.$emit('change', { style: 'inherit' }) } else { this.$emit('change', { style: v }) }
      }
    },
    fontWeight: {
      get () {
        let weight = this.value.weight
        if (weight === 'inherit') { weight = undefined }
        return weight
      },
      set (v) {
        if (v === undefined) { this.$emit('change', { weight: 'inherit' }) } else { this.$emit('change', { weight: v }) }
      }
    },
    fontSize: {
      get () {
        let size = this.value.size
        if (size === 'inherit') {
          size = undefined
        } else { size = parseFloat(this.value.size.replace(/[^.0-9]/gim, '')) }
        return size
      },
      set (v) {
        let val = v + 'px'
        if (v === undefined) {
          this.$emit('change', { size: 'inherit' })
        } else {
          this.$emit('change', { size: val })
        }
      }
    },
    fontColor: {
      get () {
        let color = this.value.color
        if (color === 'inherit') { color = undefined }
        return color
      },
      set (v) {
        if (v === undefined) { this.$emit('change', { color: 'inherit' }) } else { this.$emit('change', { color: v }) }
      }
    },
    fontReg: {
      get () {
        let reg = this.value.register
        if (reg === 'inherit') { reg = undefined }
        return reg
      },
      set (v) {
        if (v === undefined) { this.$emit('change', { register: 'inherit' }) } else { this.$emit('change', { register: v }) }
      }
    }
  }
}
</script>
